body {
  background-color: #f2f2f2;
}

.main {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.App {
  margin: 0 auto;
  max-width: 1200px;
  text-align: center;
  padding: 10px;
}

.container-list {
  width: 50%;
  display: flex;
  flex-direction: column;
}

.container-info {
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 768px) {
  .main {
    flex-direction: column;
  }
  .container-list {
    width: 100%;
  }
  .container-info {
    flex-direction: column;
  }
}
