.list--container {
  background-color: white;
  width: 49%;
  box-shadow: 1px 2px 5px 0 rgba(0, 0, 0, 0.35);
  padding: 0.8rem;
  height: 100%;
  max-height: 550px;
  overflow: scroll;
  overflow-x: hidden;
  margin-bottom: 20px;
}

.list--container::-webkit-scrollbar {
  width: 7px;
  height: 7px;
}
.list--container::-webkit-scrollbar-thumb {
  background: #e8e8e8;
  border-radius: 10px;
}
.list--container::-webkit-scrollbar-thumb:hover {
  background: #e8e8e8;
}
.list--container::-webkit-scrollbar-track {
  background: #ffffff;
  border-radius: 10px;
  box-shadow: inset 7px 10px 12px #f0f0f0;
}

.list--container h3 {
  text-align: initial;
  font-weight: 400;
  font-size: 1.3rem;
}

.list--container input {
  width: 100%;
  padding: 0.5rem;
  font-size: 1rem;
}

.list--container ul {
  width: 100%;
  padding: 0;
}

.list--container ul div {
  border-bottom: 1px solid gray;
}

.list--container ul div:last-child {
  border-bottom: none;
}

.list--container ul div h4 {
  display: flex;
  justify-content: space-between;
  font-size: 1.3rem;
}

@media screen and (max-width: 768px) {
  .list--container {
    width: 100%;
  }
}
